 import DatePickes from '../../components/DatePicke/DatePicke.vue'
	export default {
	components: {
			DatePickes,
		},
	data() {
		return {
			htmlData:'<view @click="showToast"></view>',
			jsData:`export default {methods: {
				  showToast() { //显示头部
				   this.$refs.Picke.show({message:"显示在头部",position:"top"});
				}
			  }
			}`,
			tabList:[{
				mode:"date",
				name:"日期选择",
				value:[0,1,0]
			},{
				mode:"dateTime",
				name:"日期时间选择",
				value:[0,1,0,1,0]
			},{
				mode:"time",
				name:"时间选择",
				value:[1,1]
			}],
			tabIndex:0,
			dataResult:''
		}
	},
	computed:{
		mode(){//初始值
			return this.tabList[this.tabIndex].mode
		},
		defaultVal(){ //初始值
			return this.tabList[this.tabIndex].value
		}
	},
	onLoad(e) {
		
	},
	created() {
    
    },
	methods: {
		toggleTab(index){
			this.tabIndex=index;
			this.$refs.DatePickes.show();
		},
		onConfirm(e){
			console.log(e)
			this.dataResult=JSON.stringify(e);
		}
}
}

